.ChannelCard {
    margin-bottom: 12px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
}

.ChannelCard__thumbnail {
    display: inline-block;
}

.ChannelCard__thumbnail {
    margin-right: 16px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: transparent;
    overflow: hidden;
}

.ChannelCard__details {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.ChannelCard__details {
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 1.6rem;
}

.ChannelCard__link,
.ChannelCard__link:active  {
    color: #0E0F0F;
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
}

.ChannelCard__video-publish-date {
    color: rgb(136, 136, 136);
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
}


